<template>
  <div class="report-template-view">
    <!-- ==========  ========== -->
    <div class="header-section">
      <div class="header-content">
        <div class="title-container">
          <i class="fas fa-file-alt"></i>
          <h1></h1>
        </div>
        <p class="subtitle"></p>
      </div>
      <div class="header-decoration">
        <div class="decoration-item"></div>
        <div class="decoration-item"></div>
        <div class="decoration-item"></div>
      </div>
    </div>

    <!-- ==========  ========== -->
    <div class="action-section">
      <el-button type="primary" class="action-button" @click="createNewTemplate">
        <i class="fas fa-plus"></i> 
      </el-button>
      <el-button class="action-button secondary" @click="importTemplate">
        <i class="fas fa-file-import"></i> 
      </el-button>
      <el-button class="action-button tertiary" @click="exportAllTemplates">
        <i class="fas fa-file-export"></i> 
      </el-button>
      <input
          type="file"
          ref="fileInput"
          style="display: none"
          accept=".json"
          @change="handleFileImport"
      >
    </div>

    <!--  -->
    <div v-if="loading" class="loading-overlay">
      <div class="loading-content">
        <i class="fas fa-spinner fa-spin"></i>
        <span>...</span>
      </div>
    </div>

    <!-- ==========  ========== -->
    <div class="template-list">
      <div
          class="template-card"
          v-for="template in templates"
          :key="template.id"
          :class="{ 'default-template': template.isDefault }"
      >
        <div class="card-header">
          <div class="template-meta">
            <span class="template-name">{{ template.name }}</span>
            <span v-if="template.isDefault" class="default-badge">
              <i class="fas fa-star"></i> 
            </span>
          </div>

          <div class="template-actions">
            <el-tooltip effect="dark" content="" placement="top">
              <el-button circle @click="exportTemplate(template)">
                <i class="fas fa-download"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip effect="dark" content="" placement="top">
              <el-button circle @click="editTemplate(template)">
                <i class="fas fa-edit"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip
                effect="dark"
                :content="template.isDefault ? '' : ''"
                placement="top"
            >
              <el-button
                  circle
                  :type="template.isDefault ? 'success' : ''"
                  @click="setAsDefault(template)"
              >
                <i class="fas" :class="template.isDefault ? 'fa-check' : 'fa-star'"></i>
              </el-button>
            </el-tooltip>

            <el-tooltip effect="dark" content="" placement="top">
              <el-button circle type="danger" @click="deleteTemplate(template)">
                <i class="fas fa-trash"></i>
              </el-button>
            </el-tooltip>
          </div>
        </div>

        <div class="template-preview">
          <div class="preview-content" v-html="template.preview"></div>
        </div>

        <div class="card-footer">
          <div class="footer-info">
            <span class="create-date">
              <i class="fas fa-calendar-alt"></i> : {{ template.created }}
            </span>
            <span class="template-id">
              <i class="fas fa-fingerprint"></i> ID: {{ template.id }}
            </span>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="template-card add-new" @click="createNewTemplate">
        <div class="add-new-content">
          <i class="fas fa-plus-circle"></i>
          <span></span>
        </div>
      </div>
    </div>

    <!-- ==========  ========== -->
    <el-dialog
        v-model="editorVisible"
        :title="editorTitle"
        width="80%"
        custom-class="template-editor-dialog"
    >
      <div class="template-editor-container">
        <!-- ======  ====== -->
        <div class="editor-header">
          <el-input v-model="currentTemplate.name" placeholder="" />
          <el-switch
              v-model="currentTemplate.isDefault"
              active-text=""
              inactive-text=""
          />
        </div>

        <!-- ======  ====== -->
        <div class="editor-toolbar">
          <div class="toolbar-left">
            <!--  -->
            <el-button-group>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="makeBold">
                  <i class="fas fa-bold"></i>
                </el-button>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="makeItalic">
                  <i class="fas fa-italic"></i>
                </el-button>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="makeUnderline">
                  <i class="fas fa-underline"></i>
                </el-button>
              </el-tooltip>
            </el-button-group>

            <!--  -->
            <el-button-group>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="makeUnorderedList">
                  <i class="fas fa-list-ul"></i>
                </el-button>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="makeOrderedList">
                  <i class="fas fa-list-ol"></i>
                </el-button>
              </el-tooltip>
            </el-button-group>

            <!--  /  -->
            <el-button-group>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="insertLink">
                  <i class="fas fa-link"></i>
                </el-button>
              </el-tooltip>
              <el-tooltip effect="dark" content="" placement="top">
                <el-button @click="insertImage">
                  <i class="fas fa-image"></i>
                </el-button>
              </el-tooltip>
            </el-button-group>
          </div>

          <!--  -->
          <div class="toolbar-right">
            <el-select
                v-model="selectedSection"
                placeholder=""
                class="section-select"
            >
              <el-option
                  v-for="section in sections"
                  :key="section.value"
                  :label="section.label"
                  :value="section.value"
              />
            </el-select>
            <el-button type="primary" plain @click="insertSection">
              <i class="fas fa-plus"></i> 
            </el-button>
          </div>
        </div>

        <!-- ======  +  ====== -->
        <div class="editor-content">
          <textarea
              ref="markdownTextarea"
              v-model="currentTemplate.content"
              class="markdown-editor"
              placeholder="Markdown..."
          ></textarea>

          <div class="preview-pane">
            <div class="preview-header">
              <h4><i class="fas fa-eye"></i> </h4>
            </div>
            <div class="preview-content">
              <div v-html="renderedContent"></div>
            </div>
          </div>
        </div>
      </div>

      <!-- ====== Dialog  ====== -->
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="editorVisible = false"></el-button>
          <el-button type="primary" @click="saveTemplate"></el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, computed, nextTick, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { marked } from 'marked'
import DOMPurify from 'dompurify'

export default {
  setup() {
    // 
    const templates = ref([])
    const loading = ref(false)
    const fileInput = ref(null)

    // HTML
    const generatePreview = (template) => {
      return `<div class="template-preview-content">
        <h3>${template.name || ''}</h3>
        <p>: ${new Date().toLocaleDateString()}</p>
        <div class="preview-stats">
          <span class="stat-item"><i class="fas fa-code"></i> : ${(template.content || '').split('\n').length}</span>
        </div>
      </div>`
    }

    //  require.context 
    const loadTemplates = () => {
      loading.value = true
      try {
        const templateContext = require.context(
            '@/scan-template',
            false,
            /\.json$/
        )

        templates.value = templateContext.keys()
            .map(k => templateContext(k))
            .map(template => ({
              id: template.id || `tpl-${Date.now()}-${Math.random().toString(36).substr(2, 4)}`,
              name: template.name,
              content: template.content,
              isDefault: template.isDefault || false,
              created: template.created || new Date().toLocaleDateString(),
              preview: template.preview || generatePreview(template)
            }))

        // 
        if (!templates.value.some(t => t.isDefault) && templates.value.length > 0) {
          templates.value[0].isDefault = true
        }

      } catch (error) {
        console.error(':', error)
        ElMessage.error(': ' + error.message)
      } finally {
        loading.value = false
      }
    }

    const editorVisible = ref(false)
    const editorTitle = ref('')

    const currentTemplate = reactive({
      id: '',
      name: '',
      content: '',
      isDefault: false,
      created: '',
      preview: ''
    })

    //  Markdown + 
    const renderedContent = computed(() =>
        DOMPurify.sanitize(marked.parse(currentTemplate.content || ''))
    )

    // 
    const sections = ref([
      { value: 'summary', label: '' },
      { value: 'vulnerabilities', label: '' },
      { value: 'recommendations', label: '' },
      { value: 'risk_levels', label: '' },
      { value: 'compliance', label: '' },
      { value: 'executive_summary', label: '' }
    ])
    const selectedSection = ref('')

    /* ========== DOM  ========== */
    const markdownTextarea = ref(null)

    /* ==========  ==========
       / Markdown 
    ================================== */
    const wrapSelection = (before, after = '') => {
      const el = markdownTextarea.value
      if (!el) return

      const start = el.selectionStart
      const end = el.selectionEnd
      const original = currentTemplate.content

      const selectedText = original.slice(start, end)
      const newText = before + selectedText + after

      currentTemplate.content = original.slice(0, start) + newText + original.slice(end)

      // 
      nextTick(() => {
        el.focus()
        el.selectionStart = start + before.length
        el.selectionEnd = start + before.length + selectedText.length
      })
    }

    const prependLines = (prefixCb) => {
      const el = markdownTextarea.value
      if (!el) return

      const start = el.selectionStart
      const end = el.selectionEnd
      const original = currentTemplate.content

      const beforeSel = original.slice(0, start)
      const sel = original.slice(start, end)
      const afterSel = original.slice(end)

      const lines = sel.split('\n')
      const newLines = lines.map((l, idx) => prefixCb(idx, l) + l)
      currentTemplate.content = beforeSel + newLines.join('\n') + afterSel
    }

    /* ==========  ========== */
    const makeBold = () => wrapSelection('**', '**')
    const makeItalic = () => wrapSelection('_', '_')
    // Markdown  HTML
    const makeUnderline = () => wrapSelection('<u>', '</u>')

    const makeUnorderedList = () => prependLines(() => '- ')
    const makeOrderedList = () => prependLines((i) => `${i + 1}. `)

    const insertLink = () => {
      const url = window.prompt(' URL')
      if (!url) return
      const text = window.prompt(' URL') || url
      wrapSelection(`[${text}](${url})`)
    }

    const insertImage = () => {
      const url = window.prompt(' URL')
      if (!url) return
      const alt = window.prompt('alt') || ''
      wrapSelection(``)
    }

    /* ==========  ==========
        selectedSection  Markdown 
    ================================== */
    const sectionSnippets = {
      summary: `## \n\n- : \n- : \n- : \n`,
      vulnerabilities: `## \n\n| ID |  |  |\n| --- | --- | --- |\n| VUL-001 |  |  |\n`,
      recommendations: `## \n\n1. \n2. \n`,
      risk_levels: `## \n\n|  |  |\n| --- | --- |\n|  |  |\n`,
      compliance: `## \n\n- : \n- : \n`,
      executive_summary: `## \n\n> \n`
    }

    const insertSection = () => {
      if (!selectedSection.value) {
        ElMessage.warning('')
        return
      }
      const snippet = sectionSnippets[selectedSection.value]
      wrapSelection('\n' + snippet + '\n')
      selectedSection.value = ''
    }

    /* ==========  CRUD ========== */
    const createNewTemplate = () => {
      editorTitle.value = ''
      resetCurrentTemplate()
      editorVisible.value = true
    }

    const editTemplate = (tpl) => {
      editorTitle.value = ''
      Object.assign(currentTemplate, {
        ...tpl,
        isDefault: tpl.isDefault // isDefault
      })
      editorVisible.value = true
      nextTick(() => markdownTextarea.value?.focus())
    }

    const saveTemplate = () => {
      if (!currentTemplate.name) {
        ElMessage.warning('')
        return
      }

      // 
      currentTemplate.preview = generatePreview(currentTemplate)

      if (!currentTemplate.id) {
        // 
        currentTemplate.id = `tpl-${Date.now()}-${Math.random().toString(36).substr(2, 4)}`
        currentTemplate.created = new Date().toLocaleDateString()
        templates.value.push({ ...currentTemplate })
        ElMessage.success('')
      } else {
        // 
        const idx = templates.value.findIndex((t) => t.id === currentTemplate.id)
        if (idx !== -1) {
          templates.value[idx] = { ...currentTemplate }
          ElMessage.success('')
        }
      }

      editorVisible.value = false
    }

    const setAsDefault = (tpl) => {
      templates.value.forEach(t => {
        t.isDefault = t.id === tpl.id
      })
      ElMessage.success(`"${tpl.name}"`)
    }

    const deleteTemplate = (tpl) => {
      ElMessageBox.confirm(
          `"${tpl.name}"`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
      ).then(() => {
        templates.value = templates.value.filter(t => t.id !== tpl.id)

        // 
        if (tpl.isDefault && templates.value.length > 0) {
          templates.value[0].isDefault = true
        }

        ElMessage.success('')
      }).catch(() => {})
    }

    // JSON
    const exportTemplate = (template) => {
      // JSON
      const data = JSON.stringify(template, null, 2)
      const blob = new Blob([data], { type: 'application/json' })

      // 
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `${template.name.replace(/[^\w]/g, '_')}_template.json`
      document.body.appendChild(a)
      a.click()

      // 
      setTimeout(() => {
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      }, 100)

      ElMessage.success(`"${template.name}"`)
    }

    // 
    const exportAllTemplates = () => {
      if (templates.value.length === 0) {
        ElMessage.warning('')
        return
      }

      // JSON
      const data = JSON.stringify(templates.value, null, 2)
      const blob = new Blob([data], { type: 'application/json' })

      // 
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `all_templates_${new Date().toISOString().slice(0, 10)}.json`
      document.body.appendChild(a)
      a.click()

      // 
      setTimeout(() => {
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      }, 100)

      ElMessage.success(`${templates.value.length}`)
    }

    // 
    const importTemplate = () => {
      fileInput.value.click()
    }

    // 
    const handleFileImport = (event) => {
      const file = event.target.files[0]
      if (!file) return

      const reader = new FileReader()
      reader.onload = (e) => {
        try {
          const content = e.target.result
          const importedTemplates = JSON.parse(content)

          // 
          const templatesToImport = Array.isArray(importedTemplates)
              ? importedTemplates
              : [importedTemplates]

          let importedCount = 0

          templatesToImport.forEach(template => {
            // 
            if (!template.name || !template.content) {
              ElMessage.warning(': ')
              return
            }

            // ID
            const newId = `tpl-${Date.now()}-${Math.random().toString(36).substr(2, 4)}`

            // 
            templates.value.push({
              id: newId,
              name: template.name,
              content: template.content,
              isDefault: template.isDefault || false,
              created: new Date().toLocaleDateString(),
              preview: generatePreview(template)
            })

            importedCount++
          })

          // 
          event.target.value = ''

          if (importedCount > 0) {
            ElMessage.success(`${importedCount}`)
          }

        } catch (error) {
          console.error(':', error)
          ElMessage.error(': ' + error.message)
        }
      }

      reader.readAsText(file)
    }

    const resetCurrentTemplate = () => {
      Object.assign(currentTemplate, {
        id: '',
        name: '',
        content: '# \n\n## \n...',
        isDefault: false,
        created: '',
        preview: ''
      })
    }

    // 
    onMounted(() => {
      loadTemplates()
    })

    /* ==========  ========== */
    return {
      templates,
      editorVisible,
      editorTitle,
      currentTemplate,
      sections,
      selectedSection,
      renderedContent,
      markdownTextarea,
      fileInput,
      loading,

      // toolbar
      makeBold,
      makeItalic,
      makeUnderline,
      makeUnorderedList,
      makeOrderedList,
      insertLink,
      insertImage,
      insertSection,

      // CRUD
      createNewTemplate,
      editTemplate,
      saveTemplate,
      setAsDefault,
      deleteTemplate,
      exportTemplate,
      importTemplate,
      exportAllTemplates,
      handleFileImport
    }
  }
}
</script>
